// Stats Cards Component
const StatCard = ({ title, value, change, icon, color }: any) => (
  <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 hover:shadow-md transition-shadow">
    <div className="flex items-center justify-between">
      <div>
        <p className="text-sm font-medium text-gray-600">{title}</p>
        <p className="text-2xl font-bold text-gray-900 mt-1">{value}</p>
        <div className={`flex items-center mt-2 text-sm ${change >= 0 ? 'text-green-600' : 'text-red-600'}`}>
          {change >= 0 ? <ArrowUp size={14} /> : <ArrowDown size={14} />}
          <span>{Math.abs(change)}% from last month</span>
        </div>
      </div>
      <div className={`p-3 rounded-lg ${color}`}>
        {icon}
      </div>
    </div>
  </div>
);

// Quick Actions Component
const QuickAction = ({ title, description, icon, color, onClick }: any) => (
  <div 
    className="bg-white rounded-xl shadow-sm border border-gray-200 p-5 hover:shadow-md transition-all cursor-pointer group"
    onClick={onClick}
  >
    <div className="flex items-start gap-4">
      <div className={`p-3 rounded-xl ${color} group-hover:scale-110 transition-transform`}>
        {icon}
      </div>
      <div className="flex-1">
        <h3 className="font-semibold text-gray-900">{title}</h3>
        <p className="text-sm text-gray-600 mt-1">{description}</p>
      </div>
    </div>
  </div>
);

// Reminder Card Component
const ReminderCard = ({ title, date, type, urgent }: any) => (
  <div className={`flex items-center justify-between p-4 rounded-lg border-l-4 ${
    urgent ? 'bg-red-50 border-red-400' : 'bg-blue-50 border-blue-400'
  }`}>
    <div className="flex items-center gap-3">
      <div className={`p-2 rounded-lg ${urgent ? 'bg-red-100 text-red-600' : 'bg-blue-100 text-blue-600'}`}>
        <Calendar size={18} />
      </div>
      <div>
        <h4 className="font-medium text-gray-900">{title}</h4>
        <p className="text-sm text-gray-600">{date}</p>
      </div>
    </div>
    {urgent && (
      <span className="px-2 py-1 bg-red-100 text-red-600 text-xs font-medium rounded-full">
        Urgent
      </span>
    )}
  </div>
);

// Invoice Item Component
const InvoiceItem = ({ client, amount, status, date }: any) => (
  <div className="flex items-center justify-between p-4 hover:bg-gray-50 rounded-lg transition-colors">
    <div className="flex items-center gap-4">
      <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center">
        <FileText size={18} className="text-blue-600" />
      </div>
      <div>
        <h4 className="font-medium text-gray-900">{client}</h4>
        <p className="text-sm text-gray-600">{date}</p>
      </div>
    </div>
    <div className="text-right">
      <p className="font-semibold text-gray-900">AED {amount}</p>
      <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
        status === 'Paid' ? 'bg-green-100 text-green-800' : 
        status === 'Pending' ? 'bg-yellow-100 text-yellow-800' : 
        'bg-red-100 text-red-800'
      }`}>
        {status}
      </span>
    </div>
  </div>
);

